:root {
  --background-color: white;
  --primary-text-colour: #848484;
  --title-text-colour: #000;
  --primary-accent-colour: #d41443;
  --primary-accent-colour-transp: rgba(212, 20, 67, .5);
  --secondary-accent-color: #ff0a54;
  --hover-color: #d41443;
  --accent-background-color: #ffb8c9;
  --secondary-background-color: rgba(255, 184, 201, .25);
  --header-shadow: rgba(212, 20, 67, .5);
  --transition-duration: 250ms;
}

html {
  scroll-behavior: smooth;
}

body {
  background-color: var(--background-color);
  color: var(--primary-text-colour);
  letter-spacing: .02em;
  font-family: Poppins, sans-serif;
  font-size: 16px;
  line-height: 1.5;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

a {
  color: currentColor;
  text-decoration: none;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

button {
  cursor: pointer;
}

img {
  max-width: 100%;
  display: block;
}

.list {
  list-style: none;
}

.container {
  margin-left: auto;
  margin-right: auto;
  padding-left: 16px;
  padding-right: 16px;
}

@media screen and (max-width: 767px) {
  .container {
    max-width: 428px;
    min-width: 320px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 736px;
  }
}

@media screen and (min-width: 1158px) {
  .container {
    max-width: 1158px;
  }
}

.section-title {
  text-align: center;
  color: var(--title-text-colour);
  letter-spacing: .52px;
  font-size: 36px;
  font-weight: 600;
  line-height: 1.4;
}

.header {
  width: 100%;
  z-index: 10;
  box-shadow: 0 -6px 10px 5px var(--header-shadow);
  background-color: #fff;
  position: fixed;
  top: 0;
  left: 0;
}

.menu-toggle-icon {
  stroke: var(--title-text-colour);
}

.mobile-socials {
  gap: 56px;
  display: flex;
}

@media screen and (max-width: 380px) {
  .mobile-socials {
    gap: 32px;
  }
}

.nav-link {
  font-size: 28px;
  font-weight: 700;
  line-height: 1.11;
}

@media screen and (max-width: 767px) {
  .nav-item:not(:last-child) {
    margin-bottom: 40px;
  }
}

@media screen and (min-width: 768px) {
  .nav-link {
    color: var(--title-text-colour);
    letter-spacing: .24px;
    transition-property: color;
    transition-duration: var(--transition-duration);
    padding-top: 24px;
    padding-bottom: 24px;
    font-size: 16px;
    font-weight: 400;
    transition-timing-function: cubic-bezier(.4, 0, .2, 1);
    display: block;
    position: relative;
  }
}

.navigation-wrap {
  display: none;
}

.main-nav {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

@media screen and (min-width: 768px) {
  .main-nav {
    align-items: center;
    display: flex;
  }
}

@media screen and (min-width: 768px) {
  .navigation-wrap {
    align-items: center;
    gap: 120px;
    display: flex;
  }
}

@media screen and (min-width: 1158px) {
  .navigation-wrap {
    gap: 0;
  }
}

@media screen and (min-width: 768px) {
  .site-nav {
    gap: 40px;
    display: flex;
  }
}

@media screen and (min-width: 768px) {
  .nav-link:after {
    content: "";
    width: 100%;
    height: 4px;
    border-radius: 2px;
    display: block;
    position: absolute;
    bottom: -1px;
  }

  .nav-link.current:after {
    background-color: var(--hoover-color);
  }
}

.nav-link:hover, .nav-link:focus {
  color: var(--hover-color);
}

